import React, { Component } from "react";
import API from "../../utils/API";
import { Container, Form, Button, Message } from 'semantic-ui-react';
import '../Main/Main.css';

class FirstRegistration extends Component {
    constructor(props) {
        super(props);
        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            city: '',
            state: '',
            dancerType: '',
            success: false,
            error: false,
            errorMsg: ''
        };
    }

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState({ [name]: value });
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const { firstName, lastName, email, city, state, dancerType } = this.state;

        console.log(API);  // Debug line

        API.createAttendee({ firstName, lastName, email, city, state, dancerType })
            .then(res => {
                this.setState({ success: true, error: false });
            })
            .catch(err => {
                this.setState({ error: true, success: false, errorMsg: err.response.data });
            });
    }

    render() {
        const { firstName, lastName, email, city, state, dancerType, success, error, errorMsg } = this.state;

        return (
            <Container>
                <h2>Register for the Event</h2>
                <Form onSubmit={this.handleSubmit} success={success} error={error}>
                    <Form.Input
                        label="First Name"
                        placeholder="First Name"
                        name="firstName"
                        value={firstName}
                        onChange={this.handleChange}
                        required
                    />
                    <Form.Input
                        label="Last Name"
                        placeholder="Last Name"
                        name="lastName"
                        value={lastName}
                        onChange={this.handleChange}
                        required
                    />
                    <Form.Input
                        label="Email"
                        placeholder="Email"
                        name="email"
                        value={email}
                        onChange={this.handleChange}
                        required
                        type="email"
                    />
                    <Form.Input
                        label="City"
                        placeholder="City"
                        name="city"
                        value={city}
                        onChange={this.handleChange}
                        required
                    />
                    <Form.Input
                        label="State"
                        placeholder="State"
                        name="state"
                        value={state}
                        onChange={this.handleChange}
                        required
                    />
                    <Form.Input
                        label="Program"
                        placeholder="Pottery or code"
                        name="dancerType"
                        value={dancerType}
                        onChange={this.handleChange}
                        required
                    />
                    <Button type="submit" positive>Register</Button>
                    <Message success header="Registration Successful" content="You have been registered successfully!" />
                    <Message error header="Registration Failed" content={errorMsg} />
                </Form>
            </Container>
        );
    }
}

export default FirstRegistration;



