import React, { Component } from "react";
import { Sidebar, Segment, Menu, Icon } from "semantic-ui-react";
import { BrowserRouter as Router, Route, Routes, Link, NavLink } from "react-router-dom";
import Home from "../../pages/Home";
import Attendees from "../../pages/Attendees";
import BadgeGeneration from "../../pages/BadgeGeneration";
import Workshops from "../../pages/Workshops";
import WorkshopDetails from "../../pages/WorkshopDetails";
import Registration from "../../pages/Registration";
import CheckIn from "../../pages/CheckIn";
import FirstRegistration from "../FirstRegistration";
import LandingPage from "../../pages/LandingPage";
import "./Main.css";

class Main extends Component {
  state = {
    visible: false,
  };

  toggleVisibility = () => this.setState({ visible: !this.state.visible });

  render() {
    const { visible } = this.state;
    return (
      <Router>
        <div>
          <Menu fluid widths={3}>
            <Menu.Item name="menu" onClick={this.toggleVisibility}>
              <Icon name="sidebar" />
              Menu
            </Menu.Item>
            <Menu.Item id="logo" as={Link} to="/" name="logo">
              <span>Easy Events</span>
            </Menu.Item>
            <Menu.Item as={Link} to="/event/checkin" name="scan">
              <Icon name="qrcode" />
              Check-In
            </Menu.Item>
          </Menu>
          <Sidebar.Pushable as={Segment}>
            <Sidebar
              as={Menu}
              animation="push"
              width="thin"
              visible={visible}
              icon="labeled"
              vertical
              inverted
            >
              <Menu.Item as={NavLink} to="/home" name="home">
                <Icon name="home" />
                Home
              </Menu.Item>
              <Menu.Item as={NavLink} to="/event/attendees" name="attendees">
                <Icon name="users" />
                Attendees
              </Menu.Item>
              <Menu.Item as={NavLink} to="/event/badgegeneration" name="badge-generation">
                <Icon name="qrcode" />
                Badges
              </Menu.Item>
              <Menu.Item as={NavLink} to="/event/workshops" name="workshops">
                <Icon name="magic" />
                Workshops
              </Menu.Item>
              <Menu.Item as={NavLink} to="/event/registration" name="registration">
                <Icon name="pencil" />
                Registration
              </Menu.Item>
              <Menu.Item as={NavLink} to="/event/firstregistration" name="firstregistration">
                <Icon name="checkmark" />
                First Registration
              </Menu.Item>
              <Menu.Item as={NavLink} to="/event/checkin" name="check-in">
                <Icon name="checkmark" />
                Check-In
              </Menu.Item>
            </Sidebar>
            <Sidebar.Pusher>
              <Segment basic onClick={this.toggleVisibility}>
                <Routes>
                  <Route path="/" element={<Home />} />
                  <Route path="/home" element={<Home />} />
                  <Route path="/event/attendees" element={<Attendees />} />
                  <Route path="/event/badgegeneration" element={<BadgeGeneration />} />
                  <Route path="/event/workshops" element={<Workshops />} />
                  <Route path="/event/workshops/:id" element={<WorkshopDetails />} />
                  <Route path="/event/registration" element={<Registration />} />
                  <Route path="/event/checkin" element={<CheckIn />} />
                  <Route path="/event/firstregistration" element={<FirstRegistration />} />
                  <Route path="/event/landingpage" element={<LandingPage />} />
                </Routes>
              </Segment>
            </Sidebar.Pusher>
          </Sidebar.Pushable>
        </div>
      </Router>
    );
  }
}

export default Main;



